html{
    width: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
body{
    max-width: 100%;
    height: 100%;
}
.header-top{
    padding-bottom: 0px;
    background-color: rgb(22, 22, 22);
    color: rgb(199, 199, 199);
    text-align: center;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.display-result img{
    max-height:63vh;
    max-width: 100%;
}
.buttons{
    padding-top: 10px;
}
.search{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 10px;
    padding-top: -20px;
}
.search input{
    width: 80%;
    border-radius: 10px;
    background-color: rgb(22, 22, 22);
    height: 50px;
    font-size: 26px;
    color: rgb(199, 199, 199);
    text-align: center;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.search button:focus{
    outline: none;
}
.search button:hover{
    color: rgb(22, 22, 22);
    background-color: rgb(199, 199, 199);
}
.search button{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    width: 20%;
    border-radius: 10px;
    background-color: rgb(22, 22, 22);
    height: 55px;
    position: relative;
    top: -2px;
    color: rgb(199, 199, 199);
}
.search input:focus{
    outline: none;
    background-color: lightslategray;
}
.search input:hover{
    color: rgb(22, 22, 22);
    background-color: rgb(199, 199, 199);
}
.display-result{
    border: 1px grey solid;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
.top-level-display button{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    width: 50%;
    height: 30px;
    color: rgb(199, 199, 199);
    background-color: rgb(22, 22, 22);
}

.top-level-display button:hover{
    height: 30px;
    color: rgb(22, 22, 22);
    background-color: rgb(199, 199, 199);
}
#loading{
    visibility: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
}